type Framework = "SOX" | "SOC" | "ISO" | "PCI"
type Risk = "LOW" | "MED" | "HIGH" | "CRITICAL"
type Frequency = "MONTHLY" | "QUARTERLY" | "SEMIANNUAL" | "ANNUAL" | "CONTINUOUS"
type KpiStatus = "GREEN" | "YELLOW" | "RED"
type EvidenceType = "LINK" | "FILE" | "TEXT"

type Control = {
  id: string
  code: string
  name: string
  framework: Framework
  ownerName: string
  focalName: string
  frequency: Frequency
  risk: Risk
}

type KPI = {
  id: string
  controlId: string
  name: string
  description: string
  criticality: "LOW" | "MED" | "HIGH" // influencia na severidade e exigncia de evidncia/PA
  expectedRule: string
  evidenceHint: string
}

type Execution = {
  id: string
  controlId: string
  kpiId: string
  executedAt: string // ISO date
  status: KpiStatus
  evidenceType: EvidenceType
  evidenceLink?: string
  evidenceText?: string
  notes?: string
  reviewerNotes?: string
  requiresActionPlan: boolean
  actionPlanId?: string
}

type ActionPlan = {
  id: string
  title: string
  owner: string
  dueDate: string
  priority: "P0" | "P1" | "P2" | "P3"
  status: "OPEN" | "IN_PROGRESS" | "DONE" | "BLOCKED"
  description: string
}

const frequencyLabel: Record<Frequency, string> = {
  MONTHLY: "Mensal",
  QUARTERLY: "Trimestral",
  SEMIANNUAL: "Semestral",
  ANNUAL: "Anual",
  CONTINUOUS: "Contnuo",
}

function fmtDate(iso?: string) {
  if (!iso) return ""
  try {
    const d = new Date(iso)
    const dd = String(d.getDate()).padStart(2, "0")
    const mm = String(d.getMonth() + 1).padStart(2, "0")
    const yyyy = d.getFullYear()
    return `${dd}/${mm}/${yyyy}`
  } catch {
    return iso
  }
}

function todayISO() {
  const d = new Date()
  const yyyy = d.getFullYear()
  const mm = String(d.getMonth() + 1).padStart(2, "0")
  const dd = String(d.getDate()).padStart(2, "0")
  return `${yyyy}-${mm}-${dd}`
}

function statusMeta(s: KpiStatus) {
  if (s === "GREEN") return { label: "Green", icon: CheckCircle2, badge: "bg-emerald-100 text-emerald-900" }
  if (s === "YELLOW") return { label: "Yellow", icon: AlertTriangle, badge: "bg-amber-100 text-amber-900" }
  return { label: "Red", icon: XCircle, badge: "bg-red-100 text-red-900" }
}

function criticalityBadge(c: KPI["criticality"]) {
  if (c === "LOW") return "bg-muted text-foreground"
  if (c === "MED") return "bg-amber-100 text-amber-900"
  return "bg-red-100 text-red-900"
}

